.card {
  box-sizing: border-box;
  color: #060b35;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.card__top {
  justify-content: center;
  align-items: center;
  display: flex;
}

.card__imac {
  width: 160px;
  height: 134px;
}

.card__img {
  justify-content: center;
  display: flex;
}

.card__name {
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  display: flex;
}

.card__rating--stars {
  display: flex;
}

.card__rating--element {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__rating--element:last-child {
  margin-right: 0;
}

.card__rating--stars :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__rating--reviews {
  font-size: 10px;
  line-height: 14px;
}

.card__sell {
  justify-content: space-between;
  padding-top: 24px;
  display: flex;
}

.card .sell--price {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card .sell--cost {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card .button-buy {
  text-transform: uppercase;
  color: #fff;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button-buy {
  box-sizing: border-box;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
}

.card__button-buy:hover {
  cursor: pointer;
  background-color: #fff;
}

.card__button-buy:hover .button-buy {
  color: #00acdc;
}

body {
  margin: 0;
}

